.container-inscription{
  width: 100%;
 height: 50vh;
  margin-top: 50px;
}
.sub-inscription{
  width: 100%;
height:auto;
display: flex;
}
.subt-dates{
  width: 100%;
height:100%;
  margin:10px  ;
  padding: 10px ;
  box-shadow: 2px 2px 21px 3px var(--borderBlack);
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
}
.subt-dates h2{
  text-align: center;
  font-size: 2em;

}
.subt-dates h4{
  margin-left: 20px;
  font-size: 1.7em;
  padding: 10px 0;
}
.p-periodo{
  font-size: 13px;
  padding: 3px 20px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.p-periodo span{
  font-weight: bold;
  font-size: 14px;
}
.info-p{
  text-align: justify;
  padding: 10px 20px;
  font-size:14px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.requisites{
  margin-left: 50px ;
  width: 100%;
   padding: 12px 0;
}
.requisites ul li{
  font-weight: bold;
 margin-top: 2px;
  font-size: 12px;
}
.subt-section-video{
  width: 100%;
}

@media (max-width: 690px) {
  .container-inscription{
    margin-top: 20px;
      padding: 30px 10px;
  }
  .sub-inscription{
  display: block;
  }
  .subt-section-video{
    width: 100%;
    padding:2px;
  }
}